import * as Flickity from 'flickity'
import Vimeo from '@vimeo/player'

const carousel = document.querySelector('.js-carousel')
const playButton = document.querySelector('.js-play')
const pauseButton = document.querySelector('.js-pause')
const prevButton = document.querySelector('.js-prev')
const nextButton = document.querySelector('.js-next')
const fullScreenButton = document.querySelector('.js-fullscreen')
const vimeoButton = document.querySelector('.js-vimeo')

let currentSlide = {}

if (carousel) {
  const flkty = new Flickity('.js-slides', {
    prevNextButtons: false,
    pageDots: false,
    wrapAround: true,
    on: {
      ready: function () {
        defineCurrentSlide()
      }
    }
  })

  flkty.on('change', () => {
    pause()
    defineCurrentSlide()
  })

  playButton.addEventListener('click', () => {
    play()
  })

  pauseButton.addEventListener('click', () => {
    pause()
  })

  prevButton.addEventListener('click', () => {
    flkty.previous()
    defineCurrentSlide()
  })

  nextButton.addEventListener('click', () => {
    flkty.next()
    defineCurrentSlide()
  })

  fullScreenButton.addEventListener('click', () => {
    currentSlide.video.classList.toggle('is-fullscreen')
  })

  vimeoButton.addEventListener('click', () => {
    window.open(currentSlide.url, '_blank')
  })
}

async function defineCurrentSlide () {
  currentSlide.el = document.querySelector('.slide.is-selected')
  currentSlide.img = currentSlide.el.querySelector('.video__img')
  currentSlide.video = currentSlide.el.querySelector('.video__video')

  if (!currentSlide.video) {
    carousel.classList.remove('is-playable')
    return
  }

  carousel.classList.add('is-playable')

  currentSlide.player = new Vimeo(currentSlide.video, {
    autoplay: true
  })

  currentSlide.url = await currentSlide.player.getVideoUrl()

  currentSlide.img.addEventListener('click', async () => {
    play()
  })

  currentSlide.video.addEventListener('click', async () => {
    pause()
  })
}

async function play () {
  if (!currentSlide.video) {
    return
  }

  carousel.classList.add('is-playing')
  currentSlide.img.classList.add('is-hidden')
  currentSlide.video.classList.add('is-playing')
  await currentSlide.player.play()
}

async function pause () {
  if (!currentSlide.video) {
    return
  }

  carousel.classList.remove('is-playing')
  currentSlide.img.classList.remove('is-hidden')
  currentSlide.video.classList.remove('is-playing')
  currentSlide.video.classList.remove('is-fullscreen')
  await currentSlide.player.unload()
}

